.table-exit-active {
    transform: translateX(-110%);
}
.person-enter {
    transform: translateX(110%);
}
.person-enter-active {
    transform: translateX(0%);
    transition: all ease 500ms;
}
.person-exit {
    transform: translateX(0%);
    transition: all ease 500ms;
}
.person-exit-active {
    transform: translateX(110%);
}

.form-wrapper {
    overflow-x: hidden;
    height: 100%;
    position: relative;
    overflow-y: visible;
}

.form-wrapper::-webkit-scrollbar {
    width: 0px;
}
.form-wrapper::-webkit-scrollbar-track-piece {
    border-radius: 10px;
}
.table-data,
.personal-data {
    width: calc(100% - 32px);
    position: absolute;
}

.personal-data h5.text-white {
    color: #4d312c !important;
}
.personal-data .form-control:focus + .form-label,
.personal-data .form-control:valid + .form-label,
.personal-data .form-control.valid + .form-label {
    left: 8px;
    top: -14px;
    font-size: 12px;
    color: #fe6141;
    font-weight: bolder;
    background: white;
}
.personal-data .form-control {
    border-radius: 10px;
}

.modal-dialog {
    height: 100%;
}

@media only screen and (max-width: 500px) {
    .modal-content,
    .modal-dialog {
        width: 100vw;
        height: 100%;
    }

    .table-data {
        width: 100%;
    }

    .form-wrapper {
        overflow-y: scroll;
    }

    .table-data {
        width: 100%;
    }
}

@media only screen and (max-width: 760px) {
    .table-data,
    .personal-data {
        width: 100%;
    }
}

